import { useState, useEffect } from 'react';

interface UseLogoScrollAnimationOptions {
  /** Seuil de scroll en pixels pour d√©clencher l'animation (d√©faut: 50) */
  threshold?: number;
}

/**
 * Hook pour g√©rer l'animation du logo au scroll
 * 
 * Retourne un √©tat `hasScrolled` qui devient true quand l'utilisateur
 * a scroll√© au-del√† du seuil d√©fini.
 * 
 * @param options - Options de configuration
 * @returns √âtat du scroll
 */
export function useLogoScrollAnimation(options: UseLogoScrollAnimationOptions = {}) {
  const { threshold = 50 } = options;
  const [hasScrolled, setHasScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const scrollY = window.scrollY || window.pageYOffset;
      
      if (scrollY > threshold) {
        setHasScrolled(true);
      } else {
        setHasScrolled(false);
      }
    };

    // V√©rifier imm√©diatement au montage
    handleScroll();

    window.addEventListener('scroll', handleScroll, { passive: true });
    
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [threshold]);

  return { hasScrolled };
}
